import React from "react";
import { Box, Grid, Typography } from "@material-ui/core";
import ProdutoPai from "./ProdutoPai";

export default function Categoria(props) {
  let grupos = props.produtos;
  grupos = grupos.filter(produto => {
    return produto.categories[0].id === props.id;
  });

  return (
    <Box my={1}>
      <Typography variant="subtitle1" display="block" gutterBottom>
        {props.titulo}
      </Typography>
      <Grid container spacing={2}>
        {grupos.map(grupo => (
          <Grid item xs={4} key={grupo.id}>
            <ProdutoPai dados={grupo} />
            <Typography
              variant="caption"
              display="block"
              align="center"
              gutterBottom
            >
              {grupo.name}
            </Typography>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
}
