import React, { useState, lazy } from "react";
import { useSelector } from "react-redux";
import MiniCart from "./MiniCart";
import { AppBar, Card, CardHeader, CardContent } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import classNames from "classnames";

const CartItems = lazy(() => import("./CartItems"));
const Retirada = lazy(() => import("./Retirada"));
const Pagamento = lazy(() => import("./Pagamento"));

export default function Cart() {
  const cart = useSelector(state => state.cart);
  const gateways = useSelector(state => state.payment.gateways);
  const customer = useSelector(state => state.customer.data);
  const [displayBody, setDisplayBody] = useState(false);

  const useStyles = makeStyles({
    appBar: {
      top: "auto",
      bottom: 56
    },
    cartClass: {
      maxHeight: "40vh"
    },
    displayCardBody: {
      display: displayBody ? "grid" : "none"
    },
    hide: {
      display: "none"
    },
    show: {
      display: "block"
    }
  });
  const classes = useStyles();

  const toggleCardBody = () => {
    setDisplayBody(!displayBody);
  };

  const showListItemsClick = () => {
    return <CartItems cart={cart} nextClick={showRetiradaClick} />;
  };

  const showRetiradaClick = () => {
    return (
      <Retirada prevClick={showListItemsClick} nextClick={showPagamentoClick} />
    );
  };

  const showPagamentoClick = () => {
    return (
      <Pagamento
        cart={cart}
        gateways={gateways}
        customer={customer}
        prevClick={showRetiradaClick}
      />
    );
  };

  let activeItem = showListItemsClick();

  return (
    <AppBar color="primary" className={classes.appBar}>
      <Card style={{ overflow: "auto" }}>
        <CardHeader
          className="success-color text-white"
          avatar={
            <div className="d-flex flex-row">
              <div className="flex-grow-1 ">
                <strong>Seu carrinho</strong>
              </div>
              <div className="border-left border-white text-right px-2">
                <MiniCart />
              </div>
              <div className="text-right">R$ {cart.total}</div>
            </div>
          }
          onClick={toggleCardBody}
        />

        <CardContent
          className={classNames(classes.cartClass, classes.displayCardBody)}
        >
          {activeItem}
        </CardContent>
      </Card>
    </AppBar>
  );
}
